import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function ButtonAppBar() {
  const classes = useStyles();

  return (

    <div className={classes.root}>

      <AppBar position="static" color="default">

        <Toolbar>

          <Typography variant="h6" className={classes.title} >
            Lähetä rahaa helposti, nopeasti ja turvallisesti.
          </Typography>

          <Button component={Link} to="/" color="inherit"> Etusivu </Button>

          <Button component={Link} to="/valuutat" color="inherit"> Valuuttakurssit </Button>

          <Button component={Link} to="/send" color="inherit"> Lähetä rahaa </Button>

          <Button component={Link} to="/kirjaudu" color="inherit"> Kirjaudu </Button>

        </Toolbar>

      </AppBar>
      
    </div>
  );
}
