import './App.css';
import Aloitus from './components/Aloitus';
import Valuutat from './components/Valuutat';
import Send from './components/Send';
import Kirjaudu from './components/Kirjaudu';
import Rekisteroidy from './components/Rekisteroidy';
import {BrowserRouter as Router, Route} from 'react-router-dom';

function App() {

  
  return (

<Router>
      <Route path="/" exact component={Aloitus} />
      <Route path="/valuutat" component={Valuutat} />
      <Route path="/send" exact component={Send} />
      <Route path="/kirjaudu" exact component={Kirjaudu} />
      <Route path="/register" exact component={Rekisteroidy} />
</Router>

  );
}

export default App;