import { Button, DialogActions, Dialog, DialogContent, DialogContentText, DialogTitle, FormControl, Input, InputAdornment, InputLabel, OutlinedInput, TextField, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import AccountCircle from '@material-ui/icons/AccountCircle';
import { PaymentInputsWrapper, usePaymentInputs } from 'react-payment-inputs';
import images from 'react-payment-inputs/images';
import React from 'react';
import Otsikko from './Otsikko';
import Navbar from './Navbar';
import KuvaKaksi from './kuvasend.jpg';
import Footer from './Footer';

function Send(){

    const useStyles = makeStyles((theme) => ({
        root: {
          display: 'flex',
          flexWrap: 'wrap',
        },
        textField: {
          width: '400',
          
        },
      }));


    const classes = useStyles();

    const [values, setValues] = React.useState({
        amount: '',
      });

      const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
      };

      const {
        wrapperProps,
        getCardImageProps,
        getCardNumberProps,
        getExpiryDateProps,
        getCVCProps
      } = usePaymentInputs();

      const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
    setOpen(true);
    };

    const handleClose = () => {
    setOpen(false);
    };

    return (
        
        <div>

        <Otsikko> Happy Money Exchange </Otsikko>

        <Navbar />

        <img src={KuvaKaksi} style={{marginTop : 10}}/>

        <div style={{ backgroundColor : "#e9e7db", marginTop : 10, marginBottom : 10, padding : 20}}>

        <Typography style={{ marginTop : 10 }}>

         NÄIN TEET RAHALÄHETYKSEN - NOPEAA JA HELPPOA: <br />
         1. Kirjaudu sisään tunnuksellesi ( tai rekisteröidy, jos sinulla ei ole vielä tunnusta ). 
         tekeminen vaatii käyttäjätunnuksen sekä lähettäjältä,
         että lähetyksen vastaanottajalta. <br />
         2. Kirjoita haluamasi summa, päivämäärä jolloin haluat rahan siirtää, sekä maksukortin tiedot. <br />
         3. Jatka maksusuorituksen varmistukseen. <br />
         4. Maksusuoritus näkyy vastaanottajan käyttäjätunnuksella 15 minuutin sisällä.


         <br /> <br />
            
         TÄRKEÄÄ! <br />
         Älä koskaan lähetä rahaa tuntemattomalle. <br />
         Älä koskaan luovuta lähetyksen tietoja kolmannelle osapuolelle. <br />
         Ole varovainen, kun kyse on kaupasta tai tarjouksesta, joka kuulostaa liian hyvältä ollakseen totta. <br />
         
         </Typography>

         </div>

         <div style={{ marginTop : 20, backgroundColor : "white" }}>

         <FormControl style={{ marginTop : 40 }}>

         <Typography> LÄHETTÄJÄN KÄYTTÄJÄTUNNUS </Typography>

         <FormControl className={classes.margin} style={{ marginTop: 20 }}>
         <InputLabel htmlFor="input-with-icon-adornment"> Käyttäjätunnus </InputLabel>
         <Input
          id="input-with-icon-adornment"
          startAdornment={
            <InputAdornment position="start">
              <AccountCircle />
            </InputAdornment>
          }
         />
         </FormControl>

         <Typography style={{ marginTop : 20 }}> VASTAANOTTAJAN KÄYTTÄJÄTUNNUS </Typography>

         <FormControl className={classes.margin} style={{ marginTop: 20 }}>
         <InputLabel htmlFor="input-with-icon-adornment"> Käyttäjätunnus </InputLabel>
         <Input
          id="input-with-icon-adornment"
          startAdornment={
            <InputAdornment position="start">
              <AccountCircle />
            </InputAdornment>
          }
         />
         </FormControl>

         <Typography style={{ marginTop : 20, marginBottom : 20 }}> LÄHETYSTIEDOT </Typography>

         <FormControl fullWidth className={classes.margin} variant="outlined">
          <InputLabel htmlFor="outlined-adornment-amount"> Lähetettävä summa </InputLabel>
          <OutlinedInput
            id="outlined-adornment-amount"
            value={values.amount}
            onChange={handleChange('amount')}
            startAdornment={<InputAdornment position="start"> € </InputAdornment>}
            labelWidth={60}
          />
        </FormControl>

        <form className={classes.container} noValidate style={{ marginTop : 10 }}>
        <TextField style={{ width: 400 }}
        id="date"
        label="Siirtopäivä"
        type="date"
        defaultValue="2021-05-01"
        className={classes.textField}
        InputLabelProps={{
          shrink: true,
        }}
        />
        </form>

        <div>
        <PaymentInputsWrapper {...wrapperProps} style={{ marginTop : 20, width : 400 }}>
        <svg {...getCardImageProps({ images })} />
        <input {...getCardNumberProps()} />
        <input {...getExpiryDateProps()} />
        <input {...getCVCProps()} />
        </PaymentInputsWrapper>

       </div>

       <div style={{ marginTop: 20 }}>

        <Button variant="contained" color="primary" onClick={handleClickOpen} style={{ width: 400 }}>
        Jatka maksun varmennukseen
        </Button>

        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        >

        <DialogTitle id="alert-dialog-title">{"Tarkista tiedot!"}</DialogTitle>
        <DialogContent>
        <DialogContentText id="alert-dialog-description">
        Tarkista, että tiedot ovat oikein. Jatka sitten maksutavan varmennukseen.
        </DialogContentText>
        </DialogContent>
        <DialogActions>
        <Button onClick={handleClose} color="primary">
        Peruuta
        </Button>
        <Button onClick={handleClose} color="primary">
        Jatka
        </Button>
        </DialogActions>
        </Dialog>

        </div>

       <Button variant="contained" style={{ marginTop : 10, marginBottom : 20}}> PERUUTA </Button> 


         </FormControl>

         

         </div>


        <Footer />

        </div>


    );
}

export default Send;