import { Backdrop, CircularProgress, Typography} from '@material-ui/core';
import { useState, useEffect } from 'react';
import Laskuri from './Laskuri';
import KuvaValuutat from './kuvavaluutat.jpg';
import Otsikko from './Otsikko';
import Navbar from './Navbar';
import Footer from './Footer';

function Valuutat() {

  const [data, setData] = useState({
                                      valuuttakurssit : [],
                                      virhe : "",
                                      tiedotHaettu : false
                                    })


  const haeTiedot = async () => {

    try {

      const yhteys = await fetch("https://api.ratesapi.io/api/latest");
      const tiedot = await yhteys.json();    
  
      setData({
                ...data, 
                valuuttakurssit : tiedot.rates,
                tiedotHaettu : true
              });

    } catch (e) {

      setData({
                ...data, 
                virhe : "Ei yhteyttä palvelimeen. Yritä uudelleen hetken kuluttua.",
                tiedotHaettu : true
              })

    }

  }

  useEffect(() => {

    haeTiedot();

  }, []);

  return (

    <div>

    <Otsikko> Happy Money Exchange </Otsikko>

    <Navbar />

    <img src={KuvaValuutat} style={{marginTop : 10}}/>

    <div style={{ padding : 20, backgroundColor : "#e9e7db", marginTop : 20 }}>

    <Typography style={{ marginTop : 10 }}> Täältä löydät ajankohtaiset valuuttakurssit. Kurssit päivittyvät arkisin noin kello 11.00. 
                Perjantaisin päivitämme kursseja myös noin kello 16.00. Laskurin avulla voit laskea
                valitsemasi euromäärän valitsemassasi valuutassa. </Typography>

    </div>

    <div style={{ padding : 20, backgroundColor : "white", marginTop : 20 }}>

      {(data.virhe) 
      ? <Typography color="error">{data.virhe}</Typography>
      : (data.tiedotHaettu) 
        ? <Laskuri valuutat={data.valuuttakurssit}/>
        : <Backdrop open={true}>
            <CircularProgress color="inherit" />  
          </Backdrop>       
      } 

    </div>

      <Footer />

    </div>

  );
}

export default Valuutat;