import { Button, FormControl, Input, InputAdornment, InputLabel, Link, TextField, Typography } from '@material-ui/core';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Otsikko from './Otsikko';
import Navbar from './Navbar';
import KuvaKirjaudu from './kuvakirjaudu.jpg';
import Footer from './Footer';
import Rekisteroidy from './Rekisteroidy';

function Kirjaudu() {

  return (

    <div>

      <Otsikko> Happy Money Exchange </Otsikko>

      <Navbar />

      <img src={KuvaKirjaudu} style={{marginTop : 10}}/>

      <div style={{ padding : 20, backgroundColor : "white", marginTop : 20 }}>

      <FormControl>

      <InputLabel htmlFor="input-with-icon-adornment"> Käyttäjätunnus </InputLabel>
         <Input
          id="input-with-icon-adornment"
          startAdornment={
            <InputAdornment position="start">
              <AccountCircle />
            </InputAdornment>
          }
         />

        <TextField
          id="standard-password-input"
          label="Salasana"
          type="password"
          autoComplete="current-password"
        />

        <Button variant="contained" color="primary" style={{ marginTop : 20}}> Kirjaudu sisään </Button> 

        <Typography >
        <Link style={{ marginTop : 10 }} href="/register" onClick={Rekisteroidy}>
        Rekisteröidy
        </Link>
         </Typography>

      </FormControl>

      </div>

      <Footer />

      </div>

      );
}

export default Kirjaudu;