import { Typography } from '@material-ui/core';

function Otsikko(props) {

    return (
            (props.tyyli === "pieni")
            ? <Typography style={{
                fontSize : 24,
                fontFamily : 'Poiret One',
                marginBottom : 10
              }}
              >{props.children}</Typography>
            : <Typography style={{
                fontSize : 70,
                fontFamily : 'Poiret One',
                marginTop: 10,
                marginBottom : 10
              }}
              >{props.children}</Typography>
            
    );

}

export default Otsikko;