import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, FormControl, Input, InputAdornment, InputLabel, TextField } from '@material-ui/core';
import AccountCircle from '@material-ui/icons/AccountCircle';
import { makeStyles } from '@material-ui/core/styles';
import Otsikko from './Otsikko';
import Navbar from './Navbar';
import KuvaKirjaudu from './kuvakirjaudu.jpg';
import Footer from './Footer';
import React from 'react';
import Kirjaudu from './Kirjaudu';

function Rekisteroidy() {

    const useStyles = makeStyles((theme) => ({
        root: {
          display: 'flex',
          flexWrap: 'wrap',
        },
        textField: {
          width: '400',
          
        },
      }));


    const classes = useStyles();

    const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (

    <div>

      <Otsikko> Happy Money Exchange </Otsikko>

      <Navbar />

      <img src={KuvaKirjaudu} style={{marginTop : 10}}/>

      <div style={{ padding : 20, backgroundColor : "white", marginTop : 20 }}>

      <FormControl>

        <InputLabel htmlFor="input-with-icon-adornment"> Luo käyttäjätunnus </InputLabel>
         <Input
          id="input-with-icon-adornment"
          startAdornment={
            <InputAdornment position="start">
              <AccountCircle />
            </InputAdornment>
          }
         />

        <TextField
          id="standard-password-input"
          label="Luo salasana"
          type="password"
          autoComplete="current-password"
        />

        <TextField
          id="standard-password-input"
          label="Kirjoita salasana uudelleen"
          type="password"
          autoComplete="current-password"
        />

         <TextField required id="outlined-required" label="Etunimi" variant="outlined" style={{ marginTop : 20, width : 400 }} />
         <TextField required id="outlined-required" label="Sukunimi" variant="outlined" style={{ marginTop : 5, width : 400 }} />
         <TextField required id="outlined-required" label="Osoite" variant="outlined" style={{ marginTop : 5, width : 400 }} />
         <TextField required id="outlined-required" label="Puhelinnumero" variant="outlined" style={{ marginTop : 5, width : 400 }} />
         <TextField required id="outlined-required" label="Email" variant="outlined" style={{ marginTop : 5, width : 400 }} />

         <form className={classes.container} noValidate style={{ marginTop : 10 }}>
        <TextField style={{ width: 400 }}
        id="date"
        label="Syntymäaika"
        type="date"
        defaultValue="2000-01-01"
        className={classes.textField}
        InputLabelProps={{
          shrink: true,
        }}
        />
        </form>

        <div style={{ marginTop: 20 }}>

        <Button variant="contained" color="primary" onClick={handleClickOpen}>
        Rekisteröidy
        </Button>

        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        >

        <DialogTitle id="alert-dialog-title">{"Uuden käyttäjätunnuksen luominen"}</DialogTitle>
        <DialogContent>
        <DialogContentText id="alert-dialog-description">
        Rekisteröitymällä hyväksyt yleiset käyttöehtomme. Haluatko jatkaa rekisteröitymistä?
        </DialogContentText>
        </DialogContent>
        <DialogActions>
        <Button onClick={handleClose} color="primary">
        Peruuta
        </Button>
        <Button href="/kirjaudu" onClick={Kirjaudu} color="primary" autoFocus>
         Rekisteröidy
        </Button>
        </DialogActions>
        </Dialog>

        </div>

      </FormControl>

      </div>

      <Footer />

      </div>

      );
}

export default Rekisteroidy;